import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-article-dx-council.css"

export default function Home() {
  const description = `2021年7月9日（金）AM 9:00 – 9:45、横浜駅徒歩1分のコワーキングスペース「ハマポート」にて、第21回定例会を行いました。普段のオンライン定例会とは異なる、初めてのリアル開催！`
  const pageUrl = `https://www.hotsoul.jp/dx-council/first-real-meeting`
  const imageUrl = `https://www.hotsoul.jp/ogp_first-real-meeting.jpg`

  return (
    <Layout>
      <Seo title="祝・定例会リアル開催＠ハマポート" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">
        <section className="mv_ac-dx">
          <div className="mv_ac-dx-inner">
            <p className="txt1"><span>活動報告</span></p>
            <p className="txt2">やさしいDX推進協議会の<br/>活動報告をご紹介します。</p>
          </div>
        </section>

        <section className="ac-dx">
          <h2>祝・定例会リアル開催＠ハマポート</h2>
          <div className="ac-dx-cont">
            <div className="ac-dx-inner">
              <div className="item-box">
                <p className="postdate">2021.7.9</p>
                <div className="item no-border first">
                  <div className="large-img">
                    <StaticImage
                      src="../../images/main-dx-council/first-real-meeting/ph1.jpg"
                      alt="活動報告 第21回 定例会"
                      placeholder="blurred"
                    />                  
                  </div>
                  <p>
                    2021年7月9日（金）AM 9:00 – 9:45、横浜駅徒歩1分のコワーキングスペース「ハマポート」にて、第21回定例会を行いました。<br/>
                    普段のオンライン定例会とは異なる、初めてのリアル開催！<br/>
                    やはりリアルで会うと話も弾み、あっという間の楽しい45分間でした。<br/>
                    もちろん、マスクや消毒など感染症対策も万全に行いました。
                  </p>
                </div>

                <div className="item no-border">
                  <p>
                    主な議題は、「農産物直売所マップ」と「3Dマトリクスプロジェクト」。<br/>
                    「農産物直売所マップ」では、“売りたい人”と“買いたい人”をマッチングさせるアプリや、加藤さんが自転車で配達する案など、活発な議論が交わされました。<br/>
                    「3Dマトリクスプロジェクト」では、金井さんが段ボールで作成した“模型”を披露。参加メンバーたちから大きな称賛の拍手が送られました。<br/>
                    リアルで集まるとお互いの体温が感じられ、意見やアイデアも膨らみます。またこうして、時々リアル開催をしたいと皆さん口々におっしゃっていました。
                  </p>
                </div>

                <div className="item no-border">
                  <p>
                    定例会後には、株式会社ホットソウルのHP制作用に写真撮影＆インタビューの協力もしていただきました。<br/>
                    参加メンバーの皆様、本当にありがとうございました！
                  </p>
                  <div className="large-img">
                    <StaticImage
                      src="../../images/main-dx-council/first-real-meeting/ph2.jpg"
                      alt="活動報告 第21回 定例会2"
                      placeholder="blurred"
                    />                    
                  </div>
                </div>	
                
                <div className="item no-border">
                  <p>
                    2021.10.21 追記<br/>
                    10月15日に、第2回目のリアル開催を藤沢商工会議所ミーティングルームにて行いました。
                    設備もよく、皆さん意外と交通アクセスも悪くないということで、来年1月に同じく藤沢商工会議所にて、
                    3回目のリアル開催を予定しています。
                  </p>
                </div>

                <div className="flex-share">
                  <ul className="sns">
                    <li>
                      <a href="https://twitter.com/share?url=https://www.hotsoul.jp/dx-council/fist-real-meeting" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-tw.png"
                          alt="twitterシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li> 
                    <li>
                      <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/dx-council/fist-real-meeting" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-fb.png"
                          alt="facebookシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                    <li>
                      <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/dx-council/fist-real-meeting" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-line.png"
                          alt="LIMEシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                  </ul>
			          </div>
                <br/>                
                <p className="archive"><Link to={`/dx-council/articles`}>他の記事も読む</Link></p>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}
